    box-sizing: border-box;
}

* {
    margin: 0;
}
  
body {
    line-height: 1.5;
    -webkit-font-smoothing: antialiased;
}

img, picture, video, canvas, svg {
    display: block;
    max-width: 100%;
}
  
input, button, textarea, select {
    font: inherit;
}

p, h1, h2, h3, h4, h5, h6 {
    overflow-wrap: break-word;
}
  
#root, #__next {
    isolation: isolate;
}

html {
  --var-color-primary: #354034;
  --var-color-secondary: #708C7C;
  --var-color-background: #faf8f2;
  --var-color-text-primary: #354034;
  --var-color-text-secondary: #f3efe2;
}

body {
  font-family: 'Poppins', sans-serif;
  background-color: var(--var-color-background);
  width: calc(100dvw - (100dvw - 100%));
  height: 100dvh;
}

.wave {
  --mask: 
    radial-gradient(15px at 50% calc(100% - 20px), #000 99%, #0000 101%) calc(50% - 20px) 0/40px 100%, 
    radial-gradient(15px at 50% calc(100% + 10px), #0000 99%, #000 101%) 50% calc(100% - 8px)/40px 100% repeat-x;
  -webkit-mask: var(--mask);
  mask: var(--mask);
  background-color: var(--var-color-secondary);
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-bottom: 15px;
}

.wave p {
  color: #f6f6f9;
  font-size: minmax(.90rem, 1rem);
  font-style: italic;
}

header {
  display: flex;
  align-items: center;
  justify-content: space-between; 
  background-color: transparent;
  width: 100%;
  padding: 20px 25px;
}

header img {
  height: 30px;
  width: 30px;
  border-radius: 5px;
}

header h1 {
  font-family: 'Sevillana', sans-serif;
  font-size: 2rem;
  color: var(--var-color-primary);
}

.header-buttons {
  display: flex;
  align-items: center;
  gap: 20px;
}

.header-buttons button {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--var-color-primary);
  width: 120px;
  height: 50px;
  color: #f6f6f9;
  border: none;
  border-radius: 4px;
  transition: all 0.150s ease-in-out;
  font-size: 1rem;
}

header button:hover {
  background-color: #302b2b;
  scale: 1.1;
}

#content {
  position: fixed;
  width: 100%;
}

footer {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 5px;
  padding: 40px 0 20px 0;
}

/* home page */

.homepage {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  padding: 20px;
  background-color: transparent;
  width: 100%;
  height: 100%;
}

.matcha-image {
  border-radius: 5px;
  width: 1000px;
  height: auto;
}

.homepage .heading-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px;
  gap: 5px;
  text-align: center;
}

.main-heading span {
  color: var(--var-color-background);
  background-color: var(--var-color-primary);
  padding: 3px 5px;
}

.matcha-mascot {
  position: absolute;
  bottom: 0;
  right: -100px;
  width: 300px;
  height: 300px;
}

/* menu page */

.menu-page {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 40px;
  background-color: transparent;
  width: 100%;
  height: 100%;
}

.menu-page .main-heading {
  color: var(--var-color-primary);
  margin-bottom: 8px;
}

.menu-items {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  text-align: center;
  flex-wrap: wrap;
  padding: 40px;
  background-color: transparent;
  width: 100%;
  height: 100%;
  gap: 20px;
  flex: 1;
}

.item1-container,
.item2-container,
.item3-container,
.item4-container,
.item5-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
  gap: 8px;
  width: 300px;
  min-width: fit-content;
  text-align: center;
  border: 3px solid var(--var-color-primary);
  border-radius: 6px;
  background-color: var(--var-color-secondary);
  color: var(--var-color-text-secondary);
}

.item1-container:hover,
.item2-container:hover,
.item3-container:hover,
.item4-container:hover,
.item5-container:hover {
  background-color: rgb(202, 183, 183);
  color: var(--var-color-text-primary);
}

.item1-container img,
.item2-container img,
.item3-container img,
.item4-container img,
.item5-container img {
  border-radius: 5px;
  border: 2px solid var(--var-color-primary);
}

/* about page */

.about-page {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  padding: 40px;
  background-color: transparent;
  width: 100%;
  height: 100%;
  text-align: justify;
}

.about-page .heading-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
  gap: 5px;
  text-align: justify;
  width: 500px;
}

.about-page .main-heading {
  color: var(--var-color-primary);
}

.about-page .minion-image {
  border-radius: 5px;
  width: 700px;
  min-width: fit-content;
  height: auto;
  border: 2px solid var(--var-color-primary);
}

/* contact page */

.contact-page {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 40px;
  background-color: transparent;
  width: 100%;
  height: 100%;
  text-align: justify;
}

.contact-page .main-heading {
  color: var(--var-color-primary);
  margin-bottom: 8px;
}

.contact-page .contact-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 40px 50px;
  gap: 20px;
  font-size: 1.125rem;
  text-align: justify;
  margin-top: 30px;
  background-color: var(--var-color-primary);
  color: var(--var-color-text-secondary);
  border-radius: 8px;
}

